body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

  .App {
    text-align: center;
  }

  .App-header {
    background-color: #174270;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }

  .jumbotorn{
    height: 25rem;
  }
  .about-us{
    font-family: 'Ubuntu', sans-serif;
    margin: 2rem;
  }
  .sochem {
    margin-top: 100px;
  }

  .alternate-bgcolor:nth-of-type(even) {
    background: #E6F0F2
  }
  .home-lower-text{
    color: white;
    height: 25rem;
    text-align: center;
  }

  #home-forum-name{
    font-family: 'Tajawal', sans-serif;
  }

  .top-image-home{
    height: 30rem;
    object-fit:cover;
    width: 100%;
    border-radius: 0;
    margin: 0;
  }

 a:hover{
   color: #38393a;
   text-decoration: none;
 }
  .cont-home-sochem{  
    background-color: black;
    background-image: url("https://www.transparenttextures.com/patterns/45-degree-fabric-light.png");
    width: 100%;
    margin-top: 4rem;
    margin: 0;
    margin-bottom: 3rem;
    border-radius: 0;
  }

  .about-us-div{
    margin-top: 5rem;
    padding: 2rem;
  }

  .home-updates-forum{
    font-family: 'Oswald', sans-serif;
    letter-spacing: 1px;
  }

  .home-about-text{
    font-family: 'Montserrat', sans-serif;
    opacity: 0.8;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
  }
  .home-updates{
    font-family: 'Ubuntu', sans-serif;
    font-size: 1.2rem;
    font-weight: 600;
  }
  #home-update-a{
    color: #5A738E;
  }
  #home-update-a:hover{
    color: #551A8B;
  }

  .home-forum-text{
    font-family: 'Ubuntu', sans-serif;
    font-size: 1.3rem;
    font-weight: 590;
  }


.navbar {
    font-family: 'Karla', sans-serif;
    background-color: white;
    font-size: 1.4rem;
    font-weight: 900;
    margin: 0;
    padding: 0.2rem;
}

.nav-item {
    margin-right: 2rem;
}

.navbar-light .navbar-nav .nav-link {
    color: black;
}


#sochem-home-heading{
    font-family: 'Cabin', sans-serif;
    font-size: calc(1vw + 4vh + 1vmin);
    font-weight: bolder;
    letter-spacing: 0.17rem;
    color:  white;
}
.cont-home-sochem-jb{  
    background-color:#174270;
    background-image: url("https://www.transparenttextures.com/patterns/45-degree-fabric-light.png");
